'use client'

import { useState, useEffect } from 'react'
import { 
  TrendingUp, 
  Heart, 
  Eye, 
  MessageCircle,
  ChevronRight,
  Loader2
} from 'lucide-react'
import Link from 'next/link'

interface Post {
  id: string
  title: string
  koreanTitle?: string
  content: string
  category: string
  imageUrl?: string
  likes: number
  views: number
  commentCount: number
  createdAt: string
  updatedAt: string
}

export function CategorySection() {
  const [posts, setPosts] = useState<Post[]>([])
  const [loading, setLoading] = useState(true)
  const [selectedCategory, setSelectedCategory] = useState<string>('ALL')

  const categories = [
    { id: 'ALL', name: 'All Posts', emoji: 'ðŸŒ' },
    { id: 'HUMOR', name: 'K-Humor', emoji: 'ðŸ˜‚' },
    { id: 'CULTURE', name: 'K-Culture', emoji: 'ðŸ®' },
    { id: 'DRAMA', name: 'K-Drama', emoji: 'ðŸ“º' },
    { id: 'LIFESTYLE', name: 'K-Lifestyle', emoji: 'ðŸŒ¸' },
    { id: 'TECH', name: 'K-Tech', emoji: 'ðŸ’»' },
    { id: 'GENERAL', name: 'General', emoji: 'ðŸ’¬' },
  ]

  const fetchPosts = async (category?: string) => {
    try {
      setLoading(true)
      const url = category && category !== 'ALL' 
        ? `/api/posts?category=${category}&limit=20`
        : `/api/posts?limit=20`
      
      const response = await fetch(url)
      const data = await response.json()
      
      setPosts(data.posts || [])
    } catch (error) {
      console.error('Failed to fetch posts:', error)
      setPosts([])
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchPosts(selectedCategory)
  }, [selectedCategory])

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    const now = new Date()
    const diffInHours = Math.floor((now.getTime() - date.getTime()) / (1000 * 60 * 60))
    
    if (diffInHours < 1) return 'Just now'
    if (diffInHours < 24) return `${diffInHours}h ago`
    if (diffInHours < 168) return `${Math.floor(diffInHours / 24)}d ago`
    return date.toLocaleDateString()
  }

  const truncateContent = (content: string, maxLength: number = 150) => {
    if (content.length <= maxLength) return content
    return content.substring(0, maxLength) + '...'
  }

  return (
    <>
      {/* Category Filter */}
      <section className="mb-8">
        <div className="flex flex-wrap gap-3 justify-center">
          {categories.map((category) => (
            <button
              key={category.id}
              onClick={() => setSelectedCategory(category.id)}
              className={`flex items-center gap-2 px-3 py-1 rounded-full transition-colors ${
                selectedCategory === category.id
                  ? 'bg-blue-600 text-white'
                  : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
              }`}
            >
              <span>{category.emoji}</span>
              <span className="font-medium text-sm">{category.name}</span>
            </button>
          ))}
        </div>
      </section>

      {/* Posts Section */}
      <section>
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-xl font-bold text-gray-900 flex items-center gap-2">
            <TrendingUp className="h-6 w-6 text-blue-600" />
            {selectedCategory === 'ALL' ? 'Latest Posts' : categories.find(c => c.id === selectedCategory)?.name}
          </h2>
          <div className="text-sm text-gray-500">
            {posts.length} posts found
          </div>
        </div>

        {loading ? (
          <div className="flex justify-center items-center py-12">
            <Loader2 className="h-8 w-8 animate-spin text-blue-600" />
            <span className="ml-2 text-gray-600">Loading posts...</span>
          </div>
        ) : posts.length === 0 ? (
          <div className="text-center py-12">
            <div className="text-gray-400 mb-4">
              <MessageCircle className="h-16 w-16 mx-auto" />
            </div>
            <h3 className="text-xl font-medium text-gray-900 mb-2">No posts yet</h3>
            <p className="text-gray-600 mb-6">Be the first to share Korean culture with the world!</p>
            <Link 
              href="/write"
              className="inline-flex items-center gap-2 bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 transition-colors"
            >
              Write First Post
              <ChevronRight className="h-4 w-4" />
            </Link>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {posts.map((post) => (
              <Link key={post.id} href={`/post/${post.id}`} className="block">
                <article className="bg-white rounded-lg shadow-sm border hover:shadow-md transition-shadow cursor-pointer">
                  {post.imageUrl && (
                    <div className="aspect-video overflow-hidden rounded-t-lg bg-gray-100 flex items-center justify-center relative">
                      <img 
                        src={post.imageUrl} 
                        alt={post.title}
                        className="absolute inset-0 w-full object-cover hover:scale-105 transition-transform duration-300"
                        style={{transformOrigin: 'top', objectFit: 'cover', objectPosition: 'top' }}
                        onLoad={() => console.log('Image loaded:', post.imageUrl)}
                        onError={(e) => console.error('Image failed to load:', post.imageUrl, e)}
                      />
                    </div>
                  )}
                  
                  <div className="p-5">
                    <div className="flex items-center gap-2 mb-3">
                      <span className="bg-blue-100 text-blue-700 px-2 py-1 rounded-full text-xs">
                        {categories.find(c => c.id === post.category)?.name || post.category}
                      </span>
                      {/* <div className="flex items-center text-xs text-gray-500">
                        <Clock className="h-3 w-3 mr-1" />
                        {formatDate(post.createdAt)}
                      </div> */}
                    </div>
                    
                    <h3 className="font-bold text-gray-900 mb-2 text-lg leading-tight line-clamp-2 hover:text-blue-600 transition-colors">
                      {post.title}
                    </h3>
                    
                    {post.koreanTitle && (
                      <p className="text-sm text-gray-600  mb-2">
                        Original: {post.koreanTitle}
                      </p>
                    )}
                    
                    <p className="text-gray-600 text-sm leading-relaxed mb-4 line-clamp-3">
                      {truncateContent(post.content)}
                    </p>
                    
                    <div className="flex items-center justify-between pt-4 border-t border-gray-100">
                      <div className="flex items-center gap-4 text-xs text-gray-500">
                        <div className="flex items-center gap-1">
                          <Heart className="h-4 w-4" />
                          <span>{post.likes}</span>
                        </div>
                        <div className="flex items-center gap-1">
                          <Eye className="h-4 w-4" />
                          <span>{post.views}</span>
                        </div>
                        <div className="flex items-center gap-1">
                          <MessageCircle className="h-4 w-4 text-blue-600" />
                          <span className="font-medium">{post.commentCount || 0}</span>
                        </div>
                      </div>
                      
                      <div className="text-gray-500 text-xs">
                        Updated {formatDate(post.updatedAt)}
                      </div>
                    </div>
                  </div>
                </article>
              </Link>
            ))}
          </div>
        )}
      </section>
    </>
  )
} 